.example__container {
  background-color: #f2f2f2;
  padding-top: 25px;
  padding-right: 20px;
  padding-left: 20px;
}
.example__diet__number,
.example__price__text {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
}
.example__diet__number {
  font-size: 24px;
  line-height: 24px;
}
.example__diet__text {
  font-family: "Lato", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  color: #444444;
}
.example__price__text {
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
}
